:root{--secondary-color: rgb(0, 51, 255);--primary-color: rgb(255, 0, 102);--secondary-color-opacity: rgba(0, 51, 255, 0.1);--primary-color-opacity: rgba(255, 0, 102, 0.1);--padding-normal: 15px}
*{font-family:"Poppins",sans-serif}.page-container{min-height:calc(100vh - 52px)}.page-padding{padding-left:15px;padding-right:15px;padding-bottom:15px}.btn-secondary{background-color:var(--primary-color);padding:15px 20px;color:#fff;height:auto;width:auto}.btn-secondary:focus{color:#fff;border-color:var(--primary-color);background-color:var(--primary-color)}.btn-secondary:hover{color:#fff;border-color:var(--primary-color);background-color:var(--primary-color)}.notification{border-radius:25px}.pa-2{padding:8px}
.slider-container{border-radius:25px;width:100%;height:80vh;background-color:#f0f7fd;background-position:95% center;background-repeat:no-repeat;background-size:auto 90%}.slider-container .slider-text{position:absolute;width:80%;top:20vh;height:100%;left:20%;text-align:left;font-size:2.1rem}.caracteristicas{min-height:700px;background-position:center 40%;background-repeat:no-repeat;background-size:92% auto}.caracteristicas h1{padding:40px 0px 0px 20%}.caracteristicas-cards{position:relative;margin:0 auto;margin-top:20%;width:90%}.caracteristicas-card{background-color:#f0f7fd;border:none;border-radius:10px;min-height:150px}.faqs-container{background-color:#fef2f6;border-radius:25px;width:100%;background-position:95% center;background-repeat:no-repeat;background-size:auto 90%;padding:25px;margin-top:25px}.faqs-container .slider-text{position:absolute;width:80%;top:20vh;height:100%;left:20%;text-align:left;font-size:2.1rem}.ant-collapse-borderless{background-color:transparent !important}.ant-collapse-borderless>.ant-collapse-item{margin-bottom:16px;background:transparent !important;border:0px !important}.question{font-weight:700;padding-bottom:4px}
.results-content{background-color:#f0f2f5;border-radius:25px;width:100%;height:95%;padding:25px}
.nav-router{display:flex;padding-left:15px;padding-right:15px}.nav-router .logo-container{width:50%;display:flex;align-items:center;padding-left:50px}.nav-router .logo-container .logo{height:20px}.nav-router .menu{width:50%;display:flex;justify-content:flex-end;margin-bottom:0px}.nav-router .menu .link{list-style:none;padding:15px 15px}.nav-router .menu .link-icon{color:var(--primary-color)}.nav-router .menu .link-text{color:#222}
